<template>
  <div class="Heatmap">
    <div class="title">Heatmap</div>
       
    <a-divider style="margin:0.625rem 0;" />

    <div class="data-transformation">
      <div class="title-second">Data transformation:</div>
      <a-radio-group @change="onChange" v-model="value">
        <a-radio :value="1">log</a-radio>
        <a-radio :value="2">zscore</a-radio>
      </a-radio-group>
    </div>

    <a-divider dashed style="margin:0.625rem 0;" />

    <div class="conserve-data">
      <button class="button" @click="conserveData">conserve</button>
    </div>

  </div>
</template>

<script>
export default {
  name: "Heatmap",
  data(){
    return{
      params:{
        data:{},
      },
      value:1,
    }
  },
   methods:{
    onChange(e){
      // console.log(e.target.value)
      if(e.target.value == 1){
        this.params.quantification = 'log'
      }else{
        this.params.quantification = 'zscore'
      }
    },

    //提交信息
    conserveData(){
      // console.log(this.params)
      var name = this.$store.state.id
      sessionStorage.setItem(name,JSON.stringify(this.params));
    },
  }
};
</script>

