<!--
* @componmentName nodeList
* @module "./components/nodeList"
* @fileDescription 节点选择列表
* @author li
* @date 2020年8月26日
* @param {Function} [nodeListClick] 传递一个点击事件
* @example 调用示例
* <nodeList @nodelistclick="nodeListClick"></nodeList>
 -->

<template>
  <div class="nodeList">
    <!-- 功能选择区域 -->
    <div class="slider">
      <!-- 选择区域title -->
      <div class="slider-title">Function options</div>
      <!-- 选择区域menu -->
      <div class="slider-menu">
        <a-menu
          mode="vertical"
          :open-keys="openKeys"
          style="width: 100%"
          @openChange="onOpenChange"
        >
          <a-sub-menu
            v-for="items in projectData"
            :key="items.name"
            :title="items.name"
            style="width: 85%; margin-left: 15%"
          >
            <a-menu-item
            class="menu-text"
              v-for="item in items.stateData"
              :key="item.id"
              @click="
                workflow(
                  items.projectId,
                  items.projectName,
                  item.workflowId,
                  item.resultId,
                  item.name
                )
              "
              >{{ item.name }}</a-menu-item
            >
          </a-sub-menu>
        </a-menu>
      </div>
    </div>

    <div class="clear"></div>
  </div>
</template>

<script>
export default {
  name: "nodeList",
  components: {},
  data() {
    return {
      rootSubmenuKeys: ["sub1"],
      openKeys: ["sub1"],
      projectData: [],
    };
  },

  mounted() {
    this.getProjectData();
  },

  methods: {
    onOpenChange(openKeys) {
      // ant的逻辑，主要是控制展示
      const latestOpenKey = openKeys.find(
        (key) => this.openKeys.indexOf(key) === -1
      );
      if (this.rootSubmenuKeys.indexOf(latestOpenKey) === -1) {
        this.openKeys = openKeys;
      } else {
        this.openKeys = latestOpenKey ? [latestOpenKey] : [];
      }
    },

    nodeClick(e, f) {
      // 更新vuex 的project 的id和name
      let obj = {
        id: e,
        name: f,
      };
      this.$store.commit("getProjectId", obj);
    },

    workflow(e, f, id, rId, iName) {
      let obj = {
        id: e,
        name: f,
      };
      this.$store.commit("getProjectId", obj);
      this.$emit("nodelistclick", [id, rId, iName]);
    },

    // 请求总项目数
    getProjectData() {
      this.$progress.start(); // 进度条加载
      this.$ProjectModels
        .getProjectsModels()
        .then((res) => {
          var data = res.data.projects;

          data.forEach((ele) => {
            var id = ele.projectId;
            console.log(ele);
            this.$ProjectModels
              .getProjectResultsModels(id)
              .then((val) => {
                // console.log(val.data)
                if (val.data.length == 0) {
                  ele.stateData = []; // 没有创建任务的push一个表示为空的数组
                } else {
                  ele.stateData = val.data;
                }
                this.loading = false;
                this.$progress.finish(); // 进度条加载
              })
              .catch((error) => {
                this.$message.error(
                  "Failed to update the task list. Please try again or check the network." +
                    "error:" +
                    error,
                  5
                );
              });
            this.projectData.push(ele);
          });
        })
        .catch((error) => {
          this.$message.error(
            "The request list failed. Please try again or check the network." +
              "(" +
              error +
              ")",
            5
          );
        });
    },
  },
};
</script>

<style lang="less" scoped>
.nodeList {
  width: 20rem;
  .slider {
    width: 100%;
    padding: 1.25rem 0;
    background: #fff;
    box-shadow: 0.125rem 0.125rem 1.25rem rgba(25, 25, 25, 0.1);
    border-radius: 1.5625rem;
    .slider-title {
      padding: 0 1.5rem;
      font-size: 1.5rem;
      font-weight: 550;
      font-style: italic;
      margin: 0.75rem 0;
      overflow: hidden;
      
      cursor: help;
      color: #4061ad;
    }
    /deep/.ant-menu-submenu-title{
      padding: 0 2.4rem 0 0;
    }
  }
  .clear {
    clear: both;
  }
}
</style>