<!--
* @componmentName toolMain
* @module "./components/toolMain"
* @fileDescription 节点信息收集展示
* @author li
* @date 2020年8月25日
* @param 
* @example 调用示例
* <toolMain></toolMain>
 -->

<template>
  <div class="toolMain">
    <div class="container">
      <div class="slider">
        <!-- 节点列表 -->
        <nodeList @nodelistclick="nodeListClick"></nodeList>
        <!-- 结束 -->
      </div>

      <!-- 节点信息 -->
      <div class="content">
        <!-- 分析节点展示 -->
        <div v-if="isWorkflow">
          <workflow
            :workflowId="workflowId"
            :resultId="resultId"
            @handleNodeData="handleNodeData"
          ></workflow>
          <div class="delete">
            删除结果请点击 <a @click="deleteResult">这里</a>
          </div>
        </div>
        <!-- 结束 -->

        <!-- 节点内容展示 -->
        <contentView
          :nodeData_="nodeData"
          :isWorkflow="isWorkflow"
        ></contentView>
        <!-- 结束 -->

        <!-- 图表和表格 -->
        <div class="plot-table">
          <div class="plot" v-if="isPlot">
            <div class="box-title">Chart display (not modifiable)</div>
          </div>
          <div v-if="isTable">
            <div class="box-title">Table display (not modifiable)</div>
          </div>
        </div>
        <!-- 结束 -->

        <!-- 图表和表格 -->
        <div class="plot-table">
          <div class="plot" v-if="isPlot">
            <!-- echart组件 -->
            <scatter-plot
              :key="nodeData.id"
              :nodeData_="nodeData"
              :workflowId="workflowId"
            ></scatter-plot>
          </div>
          <div class="table" v-if="isTable">
            <!-- 表格组件 -->
            <scroll-table :dataHeap="dataHeap" :filterStyle="false">
              <!-- 表格头部展示 -->
              <template slot="tableHeader" slot-scope="slot">
                <div>{{ slot.title }}</div>
              </template>

              <!-- 表格内容展示 -->
              <template slot="tableBox" slot-scope="slot">
                <div v-if="typeof slot.value == 'object'" style="display: flex">
                  <div v-for="(label, key) in slot.value" :key="label">
                    <a-tag :color="colors[key]">{{ label }}</a-tag>
                  </div>
                </div>
                <div v-else>
                  <a-tooltip
                    v-if="slot.value.length > 13"
                    :key="slot.value"
                    :title="slot.value"
                    placement="top"
                  >
                    <p style="white-space: nowrap">
                      {{ `${slot.value.slice(0, 13)}...` }}
                    </p>
                  </a-tooltip>
                  <p v-else>{{ slot.value }}</p>
                </div>
              </template>
            </scroll-table>
          </div>
        </div>
        <!-- 结束 -->

        <!-- markdown文档上传修改处 -->
        <markdown></markdown>
        <!-- 结束 -->
      </div>
      <!-- 结束 -->
    </div>

    <div class="footer"></div>
  </div>
</template>

<script>
import contentView from "./toolMain/contentView";
import markdown from "./toolMain/markdown";
import workflow from "./toolMain/workflow";
import nodeList from "./toolMain/nodeList";
// echart组件
import ScatterPlot from "./toolMain/plotComponents/ScatterPlot";
// table组件
import scrollTable from "../../../../PublicComponment/scrollTable/scrollTable";
import { getStrWidth } from "../../../../utils/tableWidth";
import { mapState } from "vuex";
// 颜色数组
import colors from "../../../../utils/colors";

export default {
  name: "toolMain",

  components: {
    contentView,
    markdown,
    workflow,
    nodeList,
    ScatterPlot,
    scrollTable,
  },

  data() {
    return {
      // 流程Id
      workflowId: "",
      // 保存后的储存信息id
      resultId: "",
      // 点击后的workflow name
      workflowName: "",
      // 控制是否显示流程图
      isWorkflow: false,
      // 单个节点信息
      nodeData: {},
      // 控制plot的显示
      isPlot: false,
      // 控制表格的显示
      isTable: false,
      // 表格数据
      tableData: [],
      tableHeader: [],
      tableCloumn: {},
      // yanse
      colors: [],
    };
  },

  computed: {
    // 对表格数据进行修改
    dataHeap() {
      let arr_1 = [];

      this.tableHeader.forEach((item) => {
        let obj = {};
        for (var i in this.tableCloumn) {
          if (item == i) {
            if (item == "label") {
              obj = {
                name: item,
                type: this.tableCloumn[item].type,
                width: 200,
              };
            } else {
              obj = {
                name: i,
                type: this.tableCloumn[item].type,
                filter: true,
              };
              if (getStrWidth(item) < 7) {
                obj.width = 140;
              } else if (getStrWidth(item) > 15) {
                obj.width = 300;
              } else {
                obj.width = getStrWidth(item) * 20;
              }
            }
            if (
              this.tableCloumn[item].category &&
              this.tableCloumn[item].category.length < 5
            ) {
              obj.type = "checkbox";
            }
            if (this.tableCloumn[item].type == "string") {
              obj.list = this.tableCloumn[item].category;
            }
            if (this.tableCloumn[item].type == "number") {
              obj.max = this.tableCloumn[item].max;
              obj.min = this.tableCloumn[item].min;
            }
            if (this.tableCloumn[item].type == "checkbox") {
              obj.list = this.tableCloumn[item].category;
            }
            arr_1.push(obj);
          }
        }
      });

      let obj = {};
      obj = {
        titleData: arr_1,
        contentData: [...this.tableData],
      };

      return obj;
    },
    ...mapState({
      projectId: (state) => state.projectId.proInfo.projectId,
    }),
  },

  created() {
    // 获取颜色
    this.colors = colors.colors;
  },

  mounted() {},

  methods: {
    nodeListClick(id) {
      if (id) {
        // 控制workflow的展示
        this.isWorkflow = false;
        // 记录workflowId
        this.workflowId = id[0];
        this.resultId = id[1];
        this.workflowName = id[2];
        this.$emit("workflowName", this.workflowName);
      }
    },

    // 传递节点数据
    handleNodeData(e) {
      this.nodeData = e;
      this.getTableData();
    },

    // 获取表格数据
    getTableData() {
      let workflowId = this.workflowId;
      let nodeId = this.nodeData.id;
      let resultId = this.resultId;
      let name = this.nodeData.name;
      let id = this.projectId;
      let isFile = false;
      let blockSize = 1;
      let blockStart = 0;
      let blockNum = 10000;
      let filename = "result";
      this.$progress.start();
      console.log(resultId);
      this.$ProjectModels
        .getTableResultModelsFile(
          workflowId,
          id,
          nodeId,
          name,
          isFile,
          blockSize,
          blockStart,
          blockNum,
          filename
        )
        .then((inx) => {
          [this.tableData, this.tableHeader, this.tableCloumn] = [
            inx.data.data,
            inx.data.header,
            inx.data.columnInfo,
          ];
          if (inx.data.length == 0) {
            this.$message.warning("No task");
            window.console.log(inx);
          }
          this.$progress.finish();
        });
    },

    // 删除结果
    deleteResult() {
      let projectId = this.projectId;
      let resultId = this.resultId;
      this.$progress.start();
      this.$ProjectModels
        .deleteResultModels(projectId, resultId)
        .then((res) => {
          // 重新获取任务列表并渲染
          window.console.log(res);
          this.isWorkflow = false;
          this.$progress.finish();
        });
    },
  },

  watch: {
    resultId() {
      this.isWorkflow = true;
    },

    nodeData() {
      switch (this.nodeData.name) {
        case "GQM":
          this.isTable = false;
          this.isPlot = false;
          break;
        case "DEG":
          this.isTable = false;
          this.isPlot = false;
          break;
        case "volcanoPlot":
          this.isTable = true;
          this.isPlot = true;
          break;
        case "PCA":
          this.isTable = true;
          this.isPlot = true;
          break;
        case "FAGL":
          this.isTable = true;
          this.isPlot = false;
          break;
        case "hierarchyCluster":
          this.isTable = false;
          this.isPlot = false;
          break;
        case "correlation":
          this.isTable = false;
          this.isPlot = false;
          break;
        case "tSNE":
          this.isTable = false;
          this.isPlot = false;
          break;
        case "kmeans":
          this.isTable = false;
          this.isPlot = false;
          break;
        case "CCP":
          this.isTable = false;
          this.isPlot = false;
          break;
      }
    },
  },
};
</script>

<style lang="scss" scoped>
.toolMain {
  // margin-top: 1.875rem;
  .container {
    display: flex;
    .slider {
      // width: 16%;
      margin-right: 1.5%;
      flex: 1;
    }
    .content {
      flex: 4;
      // width: 82.5%;
      .plot-table {
        width: 100%;
        display: flex;
        .plot {
          width: 60%;
          margin-right: 2%;
        }
        .table {
          width: 38%;
          height: 100%;
          background: #fff;
          box-shadow: 0.125rem 0.125rem 1.25rem rgba(25, 25, 25, 0.1);
          border-radius: 1.5625rem;
          padding: 1.875rem;
        }
      }
    }
  }
  .box-title {
    color: #808080;
    font-weight: 550;
    margin: 1.25rem 1.875rem 0.625rem 3.125rem;
  }
  .delete {
    font-size: 0.875rem;
    font-weight: 600;
    color: #808080;
    font-style: italic;
    margin: 1.875rem 1.875rem;
  }
}
</style>