<template>
  <div class="tSNE">
    <div class="title">tSNE</div>
    <a-divider style="margin:0.625rem 0;" />

    <div class="data-transformation">
      <div class="title-second">dimensionReductionBy:</div>
      <a-radio-group @change="onChange_0" v-model="value">
        <a-radio :value="1">row</a-radio>
        <a-radio :value="2">col</a-radio>
      </a-radio-group>
    </div>

    <a-divider dashed style="margin:0.625rem 0;" />

    <div class="data-transformation">
      <div class="title-second">init:</div>
      <a-radio-group @change="onChange_1" v-model="index">
        <a-radio :value="1">random</a-radio>
        <a-radio :value="2">pca</a-radio>
      </a-radio-group>
    </div>

    <a-divider dashed style="margin:0.625rem 0;" />

    <div class="conserve-data">
      <button class="button" @click="conserveData">conserve</button>
    </div>
  </div>
</template>

<script>
import { mapState } from 'vuex';

export default {
  name: 'tSNE',
  data() {
    return {
      value: 1,
      dimensionReductionBy: 'row',
      index: 1,
      init: 'random',
      params: {},
      test: ''
    };
  },

  computed: {
    ...mapState({
      projectId: state => state.projectId.proInfo.projectId, //获取projectId
      workflowNodeInfo: state => state.workflowNode.workflowNodeInfo, // 暂存每个已有的节点信息
      nodeId: state => state.nodeID.nodeId // 获取当前的节点Id
    })
  },

  watch: {
    nodeId(a) {
      this.recoverData(a);
    }
  },

  mounted() {
    this.recoverData(this.nodeId);
  },

  methods: {
    onChange_0(e) {
      if (e.target.value == 1) {
        this.dimensionReductionBy = 'row';
      } else {
        this.dimensionReductionBy = 'col';
      }
    },

    onChange_1(e) {
      if (e.target.value == 1) {
        this.init = 'random';
      } else {
        this.init = 'pca';
      }
    },

    //提交信息
    conserveData() {
      let obj = {
        dimensionReductionBy: this.dimensionReductionBy,
        init: this.init
      };
      let arr = Array.from(this.workflowNodeInfo);

      arr.forEach(element => {
        if (element.id == this.nodeId) {
          element.params = obj;
        }
      });
      this.$store.commit('postworkflowNodeData', arr);
    },

    // 复用函数，复原数据
    recoverData(a) {
      var that = this;
      that.workflowNodeInfo.forEach(element => {
        // window.console.log(element)
        if (element.id == a && JSON.stringify(element.params) !== '{}') {
          // window.console.log('进入判断')
          if (element.params.dimensionReductionBy == 'row') {
            this.value = 1;
          } else {
            this.value = 2;
          }
          if (element.params.init == 'random') {
            this.index = 1;
          } else {
            this.index = 2;
          }
        }
        if (element.id == a && JSON.stringify(element.params) == '{}') {
          // window.console.log('进入判断')
          this.value = 1;
          this.index = 1;
        }
      });
    }
  }
};
</script>

