  padding: 3.125rem;
  .title {
    font-weight: 600;
  }
  .title-second {
    font-size: 0.875rem;
    margin: 0.9375rem 0 0.3125rem 0;
  }
  .content-data {
    margin: 0.625rem 0rem 1.25rem 0rem;
  }
  .conserve-data {
    text-align: center;
    margin-top: 0.9375rem;
    .button {
      background: #00c2b2;
      border-radius: 0.9375rem;
      color: #ffffff;
      width: 6.25rem;
      height: 1.875rem;
      font-size: 0.875rem;
      border: none;
      outline: none;
      cursor: pointer;
    }
    .button:hover {
      background: #2e9c9c;
    }
  }
}
</style>