<template>
  <div class="Kmeans">
    <div class="title">Kmeans</div>
    <a-divider style="margin:0.625rem 0;" />

    <div class="data-transformation">
      <div class="title-second">clusterBy:</div>
      <a-radio-group @change="onChange_0" v-model="value">
        <a-radio :value="1">row</a-radio>
        <a-radio :value="2">col</a-radio>
      </a-radio-group>
    </div>

    <a-divider dashed style="margin:0.625rem 0;" />

    <div class="data-transformation">
      <div class="title-second">Transform:</div>
      <a-select v-model="transform" style="width: 100%" @change="handleChange_0">
        <a-select-opt-group>
          <span slot="label">Log</span>
          <a-select-option value="log10">
            log
            <sub>10</sub>
          </a-select-option>
          <a-select-option value="loge">
            log
            <sub>e</sub>
          </a-select-option>
          <a-select-option value="log2">
            log
            <sub>2</sub>
          </a-select-option>
        </a-select-opt-group>
        <a-select-option value="standardScale">standardScale</a-select-option>
        <a-select-opt-group>
          <span slot="label">normalize</span>
          <a-select-option value="normalize1">
            normalize1
          </a-select-option>
          <a-select-option value="normalize2">
            normalize2
          </a-select-option>
        </a-select-opt-group>
      </a-select>
    </div>

    <a-divider dashed style="margin:0.625rem 0;" />

    <div class="data-transformation">
      <div class="title-second">init:</div>
      <a-radio-group @change="onChange_1" v-model="index">
        <a-radio :value="1">k-means++</a-radio>
        <a-radio :value="2">random</a-radio>
      </a-radio-group>
    </div>

    <a-divider dashed style="margin:0.625rem 0;" />

    <div class="data-transformation">
      <div class="title-second">clusterNum:</div>
      <div class="strict-peptide-number">
        <a-input placeholder="Cluster Number" v-model="clusterNum"/>
      </div>
    </div>

    <a-divider dashed style="margin:0.625rem 0;" />

    <div class="data-transformation">
      <div class="title-second">validationMeasure:</div>
      <a-radio-group @change="onChange_2" v-model="items">
        <a-radio :value="1">SSD</a-radio>
        <a-radio :value="2">Silhouette</a-radio>
      </a-radio-group>
    </div>

    <a-divider dashed style="margin:0.625rem 0;" />

    <div class="conserve-data">
      <button class="button" @click="conserveData">conserve</button>
    </div>
  </div>
</template>

<script>
import { mapState } from 'vuex';

export default {
  name: 'Kmeans',
  data() {
    return {
      value: 1,
      clusterBy:'row',
      transform: '',
      index:1,
      init:'k-means++',
      clusterNum:'',
      items:1,
      validationMeasure:'SSD',
    };
  },
  computed: {
    ...mapState({
      projectId: state => state.projectId.proInfo.projectId, //获取projectId
      workflowNodeInfo: state => state.workflowNode.workflowNodeInfo, // 暂存每个已有的节点信息
      nodeId: state => state.nodeID.nodeId // 获取当前的节点Id
    })
  },

  watch: {
    nodeId(a) {
      // window.console.log('kaishi')
      this.recoverData(a);
    }
  },

  mounted() {
    this.recoverData(this.nodeId);
  },

  methods: {
    
    onChange_0(e) {
      if (e.target.value == 1) {
        this.metricclusterBy = 'row';
      } else {
        this.clusterBy = 'col';
      }
    },

    handleChange_0(value) {
      window.console.log(`selected ${value}`);
      this.transform = value
    },

    onChange_1(e) {
      if (e.target.value == 1) {
        this.metric = 'k-means++';
      } else {
        this.metric = 'random';
      }
    },

     handleChange_1(value) {
      window.console.log(`selected ${value}`);
    },

    onChange_2(e) {
      if (e.target.value == 1) {
        this.validationMeasure = 'SSD';
      } else {
        this.validationMeasure = 'Silhouette';
      }
    },

    //提交信息
    conserveData() {
      let obj = {
        transform: this.transform,
        clusterBy:this.clusterBy,
        init:this.init,
        clusterNum:this.clusterNum,
        validationMeasure:this.validationMeasure
      };
      let arr = Array.from(this.workflowNodeInfo);
      arr.forEach(element => {
        if (element.id == this.nodeId) {
          element.params = obj;
        }
      });
      this.$store.commit('postworkflowNodeData', arr);
    },

    // 复用函数，复原数据
    recoverData(a) {
      var that = this;
      that.workflowNodeInfo.forEach(element => {
        if(element.id == a & JSON.stringify(element.params) !== '{}'){ // 当vuex里有储存信息时，进入判断，自动赋值
          if(element.params.clusterBy == 'row'){
            this.value = 1;
          }else{
            this.value = 2;
          }
          if(element.params.init == 'k-means++'){
            this.index = 1;
          }else{
            this.index = 2;
          }
          if(element.params.validationMeasure == 'SSD'){
            this.items = 1;
          }else{
            this.items = 2;
          }
          this.transform = element.params.transform;
          this.clusterNum = element.params.clusterNum;
        }
        if(element.id == a & JSON.stringify(element.params) == '{}'){ // 当vuex里没有储存信息时，无法进入判断，自动赋值为默认值
          this.value = 1;
          this.index = 1;
          this.items = 1;
          this.transform = "log10";
          this.clusterNum = '';
        }
      });
    }
  }
};
</script>

