<!--
* @componmentName workflow
* @module "./components/workflow"
* @fileDescription 节点信息结构图
* @author li
* @date 2020年8月25日
* @param {Object} [workflowId] 传入当前的流程ID
* @example 调用示例
* <workflow :workflowId="workflowId"  @handleNodeData="handleNodeData" ></workflow>
 -->

<template>
  <div class="workflow">
    <div v-if="loading">
      <node-canvas :nodesData="nodesData" :radius="radius" @handleNodeClick="clickNode" :key="resultId"></node-canvas>
    </div>
    <div v-else class="loading">
      <img src="../../../../../assets/icon/loading-lottie.gif" />
    </div>
  </div>
</template>

<script>
import { mapState } from 'vuex';
// 引入canvas组件
import nodeCanvas from './workflow/node';

export default {
  name: 'workflow',

  components: {
    nodeCanvas,
  },

  props: {
    workflowId: {
      type: String,
    },
    resultId: {
      type: Number,
    },
  },

  data() {
    return {
      // 请求到的节点数据
      nodesData: {},
      // 结构图的路径偏移量
      radius: 20,
      loading: false,
    };
  },

  computed: {
    ...mapState({
      projectId: (state) => state.projectId.proInfo.projectId,
      stateName: (state) => state.stateName.stateName,
    }),
  },

  mounted() {
    // 初始化，调用获取连接信息函数
    this.getNodeData();
  },

  methods: {
    clickNode(e) {
      // 点击触发事件，传递到父组件
      this.$emit('handleNodeData', e);
    },

    // 获取节点数据树
    getNodeData() {
      // projectId通过vuex获取的
      var id = this.projectId;
      // 触发请求
      this.$progress.start(); // 进度条加载
      this.$ProjectModels
        .getResultInfoModels(id, this.workflowId, this.resultId)
        .then((val) => {
          // 获取任务具体状态
          if (val.data) {
            this.nodesData = val.data;
          }
          if (val.data.nodes.length > 0) {
            let j = val.data.nodes[0].height;
            for (let i = 0; i < val.data.nodes.length; i++) {
              if (j < val.data.nodes[i].height) {
                j = val.data.nodes[i].height;
              }
            }
            this.nodesData.maxHeight = j;
          }
          this.$progress.finish(); // 进度条完成
          this.loading = true;
        })
        .catch((error) => {
          this.$message.error('Failed to get status. Please try again or check the network.' + error, 5);
        });
    },
  },

  watch: {
    // 监听数据变化，重新获取数据信息
    workflowId() {
      // 调用获取连接信息函数
      this.getNodeData();
    },
  },
};
</script>

<style lang="scss" scoped>
.workflow {
  width: 100%;
  height: 25rem;
  background: #fff;
  box-shadow: 0.125rem 0.125rem 1.25rem rgba(25, 25, 25, 0.1);
  border-radius: 1.5625rem;
  div {
    height: 100%;
    width: 100%;
  }
  .loading {
    text-align: center;
    line-height: 25rem;
    img {
      height: 6.25rem;
      width: 6.25rem;
    }
  }
}
</style>