<template>
  <div class="ScatterPlot" ref="consize" v-resize="resize">
    <div id="main" :style="{ height: screenHeight - 60 + 'px' }"></div>
  </div>
</template>

<script>
import { mapState } from "vuex";
import resize from "../../../../../../plugins/directivesSet/resize";

export default {
  name: "ScatterPlot",

  props: {
    nodeData_: {
      type: [Object, Array],
    },
    workflowId: {
      type: String,
    },
  },

  data() {
    return {
      screenHeight: 0,
      positionData: [],
      max_x: "",
      min_x: "",
      max_y: "",
      min_y: "",
      xlab: "",
      ylab: "",
      symbolSize: "",
      markArea: "",
      series: [],
      fileValue: [],
      plotData: {},
      symbolName: [],
      selectedRowKeys: [], // Check here to configure the default column
      loading_1: false,
      scroll: {},
      stateData: [],
    };
  },

  // 自定义指令
  directives: {
    resize,
  },

  computed: {
    ...mapState({
      projectId: (state) => state.projectId.proInfo.projectId,
    }),

    resultNodeId() {
      return this.nodeData_.id;
    },
    stateName() {
      return this.nodeData_.name;
    },
  },

  mounted() {
    // 触发请求
    let id = this.projectId;
    let workflowId = this.workflowId;
    let nodeId = this.resultNodeId;
    let name = this.stateName;
    this.screenHeight = this.$refs.consize.offsetHeight;
    this.drawImage();
    this.getPlotData(workflowId, id, nodeId, name);
  },
  methods: {
    drawImage() {
      let canvas = document.getElementById("main");
      // 初始化开始
      // canvas.setAttribute('width', this.$refs.consize.offsetWidth);
      // canvas.setAttribute('height', this.$refs.consize.offsetHeight);
      // 绘图函数
      var myChart = this.$echarts.init(canvas);
      myChart.resize();

      // 调用showLoading方法
      myChart.showLoading({
        text: "loading",
        color: "#c23531",
        textColor: "#000",
        maskColor: "rgba(255, 255, 255, 0.2)",
        zlevel: 0,
      });
      setTimeout(() => {
        // setOption前隐藏loading事件
        myChart.hideLoading();
        myChart.setOption(
          {
            tooltip: {
              padding: 10,
              backgroundColor: "#222e",
              borderColor: "#777",
              formatter: function (obj) {
                var value = obj.value;
                return (
                  "NAME:" + value[3] + "<br>" + value[0] + "<br>" + value[1]
                );
              },
            },
            legend: {
              top: 50,
              orient: "vertical",
              left: "right",
              data: this.symbolName,
            },

            xAxis: {
              type: "value",
              name: this.plotData.xlab,
              nameGap: 16,
              nameTextStyle: {
                color: "#808080",
                fontSize: 14,
              },
              min: this.plotData.minX,
              max: this.plotData.maxX,
              splitLine: {
                // show: false,
                lineStyle: {
                  type: "dashed",
                },
              },
              axisLine: {
                lineStyle: {
                  color: "#808080",
                },
              },
            },
            yAxis: {
              min: this.plotData.minY,
              max: this.plotData.maxY,
              type: "value",
              name: this.plotData.ylab,
              nameLocation: "end",
              nameGap: 20,
              nameTextStyle: {
                color: "#808080",
                fontSize: 16,
              },
              axisLine: {
                lineStyle: {
                  color: "#808080",
                },
              },
              splitLine: {
                // show: false,
                lineStyle: {
                  type: "dashed",
                },
              },
              scale: true,
            },
            series: this.series,
          },
          true
        );
      }, 1000);

      myChart.resize();
    },

    // 复用函数 （图和表数据）
    getPlotData(workflowId, id, nodeId, name) {
      let that = this;
      let isFile = false; // 请求图表的坐标数据，所以直接设置为false
      this.$progress.start();
      this.$ProjectModels
        .getPlotResultModelsF(workflowId, id, nodeId, name, isFile)
        .then((val) => {
          console.log(val);
          that.plotData = val.data;
          that.$store.commit("getStateName", name); // 储存点击的node name；
          that.$store.commit("pushCollects", that.plotData); //储存数据在vuex
          that.workflowName = val.data.name;
          if (val.data.length == 0) {
            that.$message.warning("No data");
          }
          this.$progress.finish();
        });
    },

    // 根据指令集的变化记录当前元素的宽高
    resize() {
      // 父元素宽高变化时调用绘图函数
      this.screenHeight = this.$refs.consize.offsetHeight;
      this.drawImage();
    },
  },

  watch: {
    // 实时对获取到的数据进行修改，并触发重绘函数
    plotData() {
      this.colors = this.plotData.colors;

      Object.keys(this.plotData.data).forEach((element) => {
        this.positionData = this.plotData.data[element].reduce(function (
          coll,
          item
        ) {
          coll.push(item);
          return coll;
        },
        this.positionData);
        this.symbolName.push(element);
        let obj = {
          name: element,
          symbolSize: 6,
          data: this.plotData.data[element],
          type: "scatter",
          itemStyle: {
            color: this.colors[element],
          },
        };
        this.series.push(obj);
      });

      this.drawImage();
      this.max_x = this.plotData.maxX;
      this.min_x = this.plotData.minX;
      this.max_y = this.plotData.maxY;
      this.min_y = this.plotData.minY;
      this.xlab = this.plotData.xlab;
      this.ylab = this.plotData.ylab;

      let arr = {
        max_x: this.max_x,
        min_x: this.min_x,
        max_y: this.max_y,
        min_y: this.min_y,
        symbolSize: "NA",
        markArea: "NA",
        color: "red",
      };
      this.fileValue.push(arr);
    },
  },
};
</script>

<style lang="scss" scoped>
.ScatterPlot {
  position: relative;
  width: 100%;
  height: 100%;
  background: #fff;
  box-shadow: 0.125rem 0.125rem 1.25rem rgba(25, 25, 25, 0.1);
  border-radius: 1.5625rem;
  padding: 1.875rem;
}
</style>