<!--
* @componmentName contentView
* @module "./components/contentView"
* @fileDescription 各个节点视图切换组件
* @author li
* @date 2020年8月26日
* @param {Object} [nodeData_] 传递用户点击的节点的具体信息
* @example 调用示例
* <contentView :nodeData_="nodeData"></contentView>
 -->

<template>
  <div class="contentView">
    <div v-if="isShow" class="center">Select your saved process and click the corresponding node to view the specific information.</div>
    <div v-if="isInfo == 1">
      <GQM :nodeData_="nodeData_"></GQM>
    </div>
    <div v-if="isInfo == 2">
      <DifferentialExpressedGenes :nodeData_="nodeData_"></DifferentialExpressedGenes>
    </div>
    <div v-if="isInfo == 3">
      <InputGeneList :nodeData_="nodeData_"></InputGeneList>
    </div>
    <div v-if="isInfo == 4">
      <FilterEXP :nodeData_="nodeData_"></FilterEXP>
    </div>
    <div v-if="isInfo == 5">
      <VolcanoPlot :nodeData_="nodeData_"></VolcanoPlot>
    </div>
    <div v-if="isInfo == 6">
      <PCAPlot :nodeData_="nodeData_"></PCAPlot>
    </div>
    <div v-if="isInfo == 7">
      <Boxplot :nodeData_="nodeData_"></Boxplot>
    </div>
    <div v-if="isInfo == 8">
      <Heatmap :nodeData_="nodeData_"></Heatmap>
    </div>
    <div v-if="isInfo == 9">
      <FunctionalList :nodeData_="nodeData_"></FunctionalList>
    </div>
    <div v-if="isInfo == 10">
      <PPI :nodeData_="nodeData_"></PPI>
    </div>
    <div v-if="isInfo == 11">
      <HierarchyCluster :nodeData_="nodeData_"></HierarchyCluster>
    </div>
    <div v-if="isInfo == 12">
      <CCP :nodeData_="nodeData_"></CCP>
    </div>
    <div v-if="isInfo == 13">
      <CORRELATION :nodeData_="nodeData_"></CORRELATION>
    </div>
    <div v-if="isInfo == 14">
      <Kmeans :nodeData_="nodeData_"></Kmeans>
    </div>
    <div v-if="isInfo == 15">
      <tSNE :nodeData_="nodeData_"></tSNE>
    </div>
  </div>
</template>

<script>
import GQM from './contentView/GQM';
import InputGeneList from './contentView/InputGeneList';
import DifferentialExpressedGenes from './contentView/DifferentialExpressedGenes';
import FilterEXP from './contentView/FilterEXP';
import VolcanoPlot from './contentView/VolcanoPlot';
import PCAPlot from './contentView/PCAPlot';
import Boxplot from './contentView/Boxplot';
import Heatmap from './contentView/Heatmap';
import FunctionalList from './contentView/FunctionalList';
import PPI from './contentView/PPI';
import HierarchyCluster from './contentView/HierarchyCluster';
import CCP from './contentView/CCP';
import CORRELATION from './contentView/CORRELATION';
import Kmeans from './contentView/kmeans';
import tSNE from './contentView/tSNE';

export default {
  name: 'contentView',
  data() {
    return {
      isShow: true,
      isInfo: 100000,
    };
  },

  props: {
    nodeData_: {
      type: Object,
    },
    isWorkflow:{
      type: Boolean
    }
  },


  components: {
    GQM,
    InputGeneList,
    DifferentialExpressedGenes,
    FilterEXP,
    VolcanoPlot,
    PCAPlot,
    Boxplot,
    Heatmap,
    FunctionalList,
    PPI,
    HierarchyCluster,
    CCP,
    CORRELATION,
    Kmeans,
    tSNE,
  },

  methods: {},

  
  watch: {
    isWorkflow(){
      this.isShow = !this.isWorkflow;
    },
    nodeData_() {
      let that = this;
      switch (that.nodeData_.name) {
        case 'GQM':
          that.isInfo = 1;
          break;
        case 'DEG':
          that.isInfo = 2;
          break;
        case 'volcanoPlot':
          that.isInfo = 5;
          break;
        case 'PCA':
          that.isInfo = 6;
          break;
        case 'FAGL':
          that.isInfo = 9;
          break;
        case 'hierarchyCluster':
          that.isInfo = 11;
          break;
        case 'correlation':
          that.isInfo = 13;
          break;
        case 'tSNE':
          that.isInfo = 15;
          break;
        case 'kmeans':
          that.isInfo = 14;
          break;
        case 'CCP':
          that.isInfo = 12;
          break;
      }
    },
  },
};
</script>

<style lang="scss" scoped>
.contentView {
  //  width: 80rem;
  .center {
    background: #fff;
    box-shadow: 0.125rem 0.125rem 1.25rem rgba(25, 25, 25, 0.1);
    border-radius: 1.5625rem;
    height: 12.5rem;
    line-height: 12.5rem;
    text-align: center;
    font-weight: 600;
    font-size: 1.25rem;
  }
  .info-card {
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap;
    .all-info,
    .half-info {
      margin-bottom: 1.25rem;
      font-size: 1.125rem;
    }
    .half-info {
      width: 40%;
      span {
        font-weight: 550;
        color: #4061ad;
      }
    }
    .all-info {
      width: 100%;
    }
  }
}
</style>